import React, { Component } from 'react';
import { Table } from 'antd';
import { ColEditDelete } from 'components/common';

export default class TableTeacherSubject extends Component {
  state = {};

  columns = [
    {
      title: 'M mn hc',
      dataIndex: 'subjectId',
      key: 'subjectId',
    },
    {
      title: 'Mn hc',
      dataIndex: 'subject',
      key: 'subject',
    },
    {
      title: 'B mn',
      dataIndex: 'subjectGroup',
      key: 'subjectGroup',
    },
    {
      title: 'Loi mn',
      dataIndex: 'subjectType',
      key: 'subjectType',
    },
    {
      title: 'Khi kin thc',
      dataIndex: 'knowledge',
      key: 'knowledge',
    },
    {
      title: 'Thao tc',
      dataIndex: 'operation',
      render: (text, record) => (
        <ColEditDelete
          hidenBtnEdit
          onConfirmDelete={() => this.props.onDelete(record.subjectId)}
        />
      ),
    },
  ];

  render() {
    const { data } = this.props;
    return (
      <Table
        bordered
        scroll={{ x: true }}
        dataSource={data}
        columns={this.columns}
        {...this.props}
      />
    );
  }
}



